<template>
  <div class="app-container">
    <van-steps direction="vertical" :active="active">
      <van-step v-for="(item, index) in list" :key="index">
        <h3>{{ item.name }}</h3>
        <p class="row">
          <span>计划  {{ item.plan_start_show }} <template v-if="item.state=='1'" />  签到  {{ item.sign_time_show }}</span>
          <van-button v-if="item.state==0 && plan_type === '2'" round type="primary" size="small" icon="edit" style="width: 70px" @click="toSign(item, index)">签字</van-button>
          <van-button v-else-if="item.state==0 && plan_type ==='1'" round type="primary" size="small" icon="scan" style="width: 70px" @click="toSign(item, index)">签到</van-button>
          <van-button v-else round type="primary" size="small" icon="eye-o" style="width: 70px" @click="toViewReport(item)">查看</van-button>
        </p>
      </van-step>
    </van-steps>
  </div>
</template>

<script>
export default {
  name: 'QcStepList',
  data() {
    return {
      active: 0,
      plan_type: '', // 巡更计划类型："1" 保洁 "2" 运送
      ticket_id: '',
      report_flag: null, // 质控报告：1 需要填写 0 不需要
      scan_flag: null, // 扫码签到：1 需要扫码签到 0 不需要
      sign_mode: null, // 签到模式：0 灵活签到 1 顺序签到
      list: []
    }
  },
  async mounted() {
    this.plan_type = this.$route.query.plan_type
    this.ticket_id = this.$route.query.ticket_id
    this.report_flag = this.$route.query.report_flag
    this.scan_flag = this.$route.query.scan_flag
    this.sign_mode = this.$route.query.sign_mode
    await this.getList()
  },
  methods: {
    async getList() {
      const res = await this.$http.post('/work-ticket/qc_step/list', { ticket_id: this.ticket_id })
      let flag = false
      for (let i = 0; i < res.length; i++) {
        if (res[i].state === 0) { // 未签到
          flag = true
          this.active = i
          break
        }
      }
      this.list = res
      if (!flag) { // 都已经签到
        this.active = -1
      }
    },
    async toSign(item, index) {
      if (this.sign_mode === '1' && index > 0) { // 顺序签到
        if (this.list[index - 1].state === 0) { // 前一个未签到
          this.$toast('只能顺序签到')
          return
        }
      }
      if (this.plan_type === '2') { // 运送巡更:签字交接
        await this.$router.push({ path: '/transport-qc/sign', query: { step_id: item.id, report_flag: this.report_flag }})
        return
      }

      // 保洁巡更
      if (this.scan_flag === '1') { // 需要扫码签到
        const qrcode = await this.$bridge.Scan()
        await this.$http.post('/work-ticket/qc_step/check-qrcode', { step_id: item.id, qrcode: qrcode })
      }
      // await this.$utils.routerUrl('/qc/step-info', { step_id: item.id, report_flag: this.report_flag })
      await this.$router.push({ path: '/qc/step-info', query: { step_id: item.id, report_flag: this.report_flag }})
    },
    toViewReport(item) {
      // this.$utils.routerUrl('/qc/step-info', { step_id: item.id })
      this.$router.push({ path: '/qc/step-info', query: { step_id: item.id }})
    }
  }
}
</script>

<style lang="less" scoped>
.app-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #f5f6fa;
  color: rgb(153, 153, 153);
  p {
    margin: 0 0 5px 0;
  }
  .row{
    display: flex;
    justify-content: space-between;
  }
}
</style>
